import request from '@/utils/request'
// 公司接口
// 获取公司列表
export function fetchCompanyList (query) {
  return request({
    url: '/company',
    method: 'Post',
    params: query
  })
}
// 新增公司
export function companyAdd (data) {
  return request({
    url: '/company/add',
    method: 'Post',
    data
  })
}
// 通过id获取公司详细信息
export function companyDetail (id) {
  return request({
    url: '/company/detail',
    method: 'Post',
    id
  })
}
// 修改公司
export function companyUpdate (data) {
  return request({
    url: '/company/update',
    method: 'post',
    data
  })
}
// 删除公司
export function companyDelete (id) {
  return request({
    url: '/company/delete',
    method: 'post',
    id
  })
}



