// 拍照组件
<template>
  <el-dialog title="拍照框" :visible.sync="Visible" width="600px" close="close">
    <web-cam ref="webcam" width="550px" height="500px" :deviceId="deviceId" @started="onStarted" @stopped="onStopped" @error="onError" @cameras="onCameras" @camera-change="onCameraChange" />
    <select v-model="camera">
      <option>-- 选择摄像头 --</option>
      <option v-for="device in devices" :key="device.deviceId" :value="device.deviceId">{{ device.label }}</option>
    </select>
    <span slot="footer" class="dialog-footer">
      <el-button @click="cancle">取 消</el-button>
      <el-button type="primary" @click="submit">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
import WebCam from '@/components/common/webcam.vue'
import { find, head } from 'lodash'
export default {
  components: {
    WebCam
  },
  data: function () {
    return {
      Visible: false,
      img: null,
      camera: null,
      deviceId: null,
      devices: []
    }
  },
  computed: {
    device: () => find(this.devices, n => n.deviceId === this.deviceId)
  },
  watch: {
    camera: function (id) {
      this.deviceId = id
    },
    devices: function () {
      // Once we have a list select the first one
      let first = head(this.devices)
      if (first) {
        // this.camera = first.label
        this.camera = first.deviceId
        this.deviceId = first.deviceId
        console.log(this.device)
      }
    }
  },
  props: ['submit'],
  methods: {
    close () {
      this.onStop()
    },
    onCapture () {
      this.img = this.$refs.webcam.capture()
      console.log(this.img)
    },
    onStarted (stream) {
      console.log('On Started Event', stream)
    },
    onStopped (stream) {
      console.log('On Stopped Event', stream)
    },
    onStop () {
      this.$refs.webcam.stop()
    },
    onStart () {
      this.$refs.webcam.start()
    },
    onError (error, stream) {
      console.log('On Error Event', error, stream)
    },
    onCameras (cameras) {
      this.devices = cameras
      console.log('On Cameras Event', cameras)
    },
    onCameraChange (deviceId) {
      this.deviceId = deviceId
      this.camera = deviceId
      console.log('On Camera Change Event', deviceId)
    },
    cancle () {
      this.Visible = !this.Visible
      this.onStop()
    },
    clear () {
      this.Visible = false
    }
  }
}
</script>

<style>
</style>



