<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-input placeholder="手机号/卡号/姓名" class="handle-input mr10"></el-input>
        <el-button type="primary" icon="search">入场</el-button>
        <el-button type="success" icon="search">清场</el-button>
        <el-button type="warning" icon="search">访客来访</el-button>
        <el-button type="warning" icon="search">离场列表</el-button>
      </div>
      <el-row>
        <el-col :span="16">
          <el-table :data="list" border style="width: 100%" fit v-loading="listLoading" @row-dblclick="handleDoubleClick" highlight-current-row>
            <el-table-column label="入场时间">
              <template slot-scope="scope">
                <span>{{ scope.row.creatdate | parseTime('{y}-{m}-{d} {h}:{i}') }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="cname" label="姓名">
            </el-table-column>
            <el-table-column prop="tel" label="卡号">
            </el-table-column>
            <el-table-column prop="address" label="卡种">
            </el-table-column>
            <el-table-column prop="creatid" label="电话">
            </el-table-column>
            <el-table-column prop="updaid" label="手牌">
            </el-table-column>
          </el-table>
        </el-col>
        <!--右边信息-->
        <el-col :span="8" class="rightinfo">
          <el-row>
            <el-col :span="12"><a @click="takePhoto"> <img class="img" :src="company.img"></a></el-col>
            <el-col :span="12" style="f">
              <h1> 唐博</h1>
              <h4> 卡号</h4>
              <h4> 卡种</h4>
              <h4> 电话</h4>
            </el-col>
          </el-row>
          <el-row :span="24" style="background:black">
            <h2 style="width:100%;color:white;text-align:center;padding:5px">基础信息</h2>
          </el-row>
          <div style="background:#cdcdcd">
            <hr>
            <el-row>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
            </el-row>
            <hr>
            <el-row>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
            </el-row>
            <hr>
            <el-row>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
            </el-row>
            <hr>
            <el-row>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
            </el-row>
            <hr>
            <el-row>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
            </el-row>
            <hr>
            <el-row>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
              <el-col :span="12">
                <h3>归属门店</h3>
              </el-col>
            </el-row>
            <hr>
            <el-row>
              <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="" value="请输入fasdfasdfadsfasfasdfasdfasdfasdfasd内容请输入fasdfasdfadsfasfasdfasdfasdfasdfasd内容请输入fasdfasdfadsfasfasdfasdfasdfasdfasd内容请输入fasdfasdfadsfasfasdfasdfasdfasdfasd内容请输入fasdfasdfadsfasfasdfasdfasdfasdfasd内容">
              </el-input>
            </el-row>
            <el-row style="margin-top:10px ;text-align:center;">
              <el-col :span="6">
                <h3 style="inline-block">水吧挂单</h3>
              </el-col>
              <el-col :span="6">
                <el-button type="primary" icon="search" size="small"> 结算</el-button>
              </el-col>
              <el-col :span="12" style="">
                <el-button type="warning" icon="search"> 离场</el-button>
              </el-col>
            </el-row>
            <div style="height:20px"></div>
          </div>
        </el-col>

      </el-row>
      <div class="pagination">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" @prev-click='prevClick' @next-click='nextClick' background layout="total, sizes, prev, pager, next, jumper" :total='total' :page-sizes="[10,15,20,50]" :page-size="size" :current-page="pageNum">
        </el-pagination>
      </div>
    </div>
    <take-photo ref="entry_takephoto" :submit="submit"></take-photo>

  </div>
</template>

<script>
import TakePhoto from '@/components/common/takephoto.vue'
import {
  fetchCompanyList,
  companyAdd,
  //   companyDetail,
  companyUpdate
  //   companyDelete
} from '@/api/company/company.js'

export default {
  components: {
    // WebCam,
    TakePhoto
  },
  data () {
    return {
      editDialog: false, // 编辑框
      addDialog: false, // 新增框
      company: {
        rowid: '',
        cname: '',
        tel: '',
        address: '',
        creatid: '',
        creatdate: '',
        updaid: '',
        updadate: '',
        remark: '',
        img: ''
      },
      listQuery: {
        page: 0, // 当前页
        size: 10 // 一页多少
      },
      listLoading: true,
      list: [],
      pageNum: 0, // 当前页
      pages: 0, // 总页数
      pageSize: 0, // 每页的数量
      size: 0, // 当前页的数量
      total: 0 // 总记录数

    }
  },
  created () {
    this.getList()
  },

  methods: {
    // 打开拍照
    takePhoto () {
      this.$refs.entry_takephoto.Visible = true
      this.$refs.entry_takephoto.onStart()
    },
    // // 关闭拍照
    // closePhoto () {
    //   this.$refs.entry_takephoto.Visible = false
    //   this.$refs.entry_takephoto.stop()
    // },
    // 确定拍照onStart
    submit () {
      this.$refs.entry_takephoto.Visible = false
      this.$refs.entry_takephoto.onCapture()
      this.company.img = this.$refs.entry_takephoto.img
      this.$refs.entry_takephoto.onStop()
    },
    // 双击添加
    addClick () {
      this.addDialog = true
      this.company = {
        cname: '',
        tel: '',
        address: '',
        creatid: '',
        creatdate: '',
        updaid: '',
        updadate: '',
        remark: ''
      }
    },
    // 新增公司
    add () {
      this.addDialog = false
      companyAdd(this.company).then(response => {
        this.getList()
        console.log('组件内数据' + response)
        this.$message({
          showClose: true,
          message: '添加成功',
          type: 'success'
        })
      })
    },
    // 双击弹出
    handleDoubleClick (row, event) {
      console.log(row)
      console.log(event)
      this.editDialog = true
      this.company = row
    },
    //   修改信息弹出
    handleClick (company) {
      this.editDialog = true
      this.company = company
    },
    // 修改数据
    updata () {
      this.editDialog = false
      companyUpdate(this.company).then(response => {
        console.log('组件内数据' + response)
        this.$message({
          showClose: true,
          message: '修改成功',
          type: 'success'
        })
      })
    },
    // 获取列表
    getList () {
      this.listLoading = true
      fetchCompanyList(this.listQuery)
        .then(response => {
          this.list = response.data.list
          this.pages = response.data.pages
          this.pageSize = response.data.pageSize
          this.total = response.data.total
          this.pageNum = response.data.pageNum
          console.log('获取列表')
          console.log(response)
          this.listLoading = false
          // this.total = response.data.data.total
          // setTimeout(() => {
          //   this.listLoading = false
          // }, 1.5 * 1000)
        })
        .catch(error => {
          console.log('组件内接受错误' + error)
          this.listLoading = false
        })
    },
    // 改变每一页多少条数据
    handleSizeChange (val) {
      this.listQuery.size = val
      this.getList()
      console.log(`每页 ${val} 条`)
    },
    // 改变当前页
    handleCurrentChange (val) {
      this.listQuery.page = val
      this.getList()
      console.log(`当前页: ${val}`)
    },
    // 上一页
    prevClick () {
      ++this.listQuery.size
      this.getList()
    }, // 下一页
    nextClick () {
      --this.listQuery.size
      this.getList()
    }
  }
}
</script>

<style scoped>
.pagination {
  margin: 20px 0;
  text-align: left;
}
.handle-box {
  margin-bottom: 10px;
}
.handle-select {
  width: 120px;
}
.handle-input {
  width: 300px;
  display: inline-block;
}
.del-dialog-cnt {
  font-size: 16px;
  text-align: center;
}
.table {
  width: 100%;
  font-size: 14px;
}
.red {
  color: #ff0000;
}
.img {
  width: 180px;
  height: 150px;
  margin: 3px 3px;
  border: 1px solid white;
}
.rightinfo {
  background: #274f68;
  padding: 2px;
}
h1,
h4 {
  margin-right: 15px;
  padding: 5px;
  padding-left: 15px;
  color: aliceblue;
}
hr {
  height: 5px;
  color: white;
}
h3 {
  width: 100%;
  color: black;
  text-align: center;
  padding: 5px;
  display: inline-block;
}
</style>



